<template>
  <el-dialog
    :title="!dataForm.id ? '新增' : '修改'"
    :close-on-click-modal="false"
    :visible.sync="visible"
  >
    <el-form
      :model="dataForm"
      :rules="dataRule"
      ref="dataForm"
      @keyup.enter.native="dataFormSubmit()"
      label-width="80px"
    >
      <el-form-item label="题目描述" prop="subjectName">
        <el-input
          v-model="dataForm.subjectName"
          placeholder="题目描述"
        ></el-input>
      </el-form-item>
      <el-form-item label="A选项描述" prop="aDescribtion">
        <el-input
          v-model="dataForm.aDescribtion"
          placeholder="A选项描述"
        ></el-input>
      </el-form-item>
      <el-form-item label="B选项描述" prop="bDescribtion">
        <el-input
          v-model="dataForm.bDescribtion"
          placeholder="B选项描述"
        ></el-input>
      </el-form-item>
      <el-form-item label="C选项描述" prop="cDescribtion">
        <el-input
          v-model="dataForm.cDescribtion"
          placeholder="C选项描述"
        ></el-input>
      </el-form-item>
      <el-form-item label="D选项描述" prop="dDescribtion">
        <el-input
          v-model="dataForm.dDescribtion"
          placeholder="D选项描述"
        ></el-input>
      </el-form-item>
      <el-form-item label="题目描述" prop="subjectImage">
        <!-- 文件上传组件 -->
        <el-upload
          action="http://175.178.189.129:11000/uploadFile/subjectImage"  
          :on-success="uploadSuccess"
          list-type="picture-card"
          :on-preview="handlePictureCardPreview"
          :on-remove="handleRemove"
        >
          <i class="el-icon-plus"></i>
        </el-upload>
        <el-dialog :visible.sync="dialogVisible">
          <img width="100%" :src="dialogImageUrl" alt="" />
        </el-dialog>
      </el-form-item>
      <el-form-item label="题目类型" prop="subjectType">
        <el-radio-group v-model="dataForm.subjectType" size="medium">
          <el-radio
            v-for="(item, index) in subjectTypeOptions"
            :key="index"
            v-model="dataForm.subjectType"
            :label="item.value"
            :disabled="item.disabled"
            >{{ item.label }}</el-radio
          >
        </el-radio-group>
      </el-form-item>
      <el-form-item label="题目答案" prop="subjectAnwser">
        <el-input
          v-model="dataForm.subjectAnwser"
          placeholder="题目答案"
        ></el-input>
      </el-form-item>

      <el-form-item label="题目分数" prop="score">
        <el-input-number
          v-model="dataForm.score"
          :min="1"
          :max="100"
          label="请输入题目分数"
        ></el-input-number>
      </el-form-item>
      <!-- 题目分数 -->
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button @click="visible = false">取消</el-button>
      <el-button type="primary" @click="dataFormSubmit()">确定</el-button>
    </span>
  </el-dialog>
</template>

<script>
export default {
  data() {
    return {
      token: "",
      dialogImageUrl: "",
      dialogVisible: false,
      subjectTypeOptions: [
        {
          label: "单选题",
          value: 0,
        },
        {
          label: "多选题",
          value: 1,
        },
        {
          label: "填空题",
          value: 2,
        },
        {
          label: "判断题",
          value: 3,
        },
        {
          label: "简答题",
          value: 4,
        },
      ],
      visible: false,
      //是否是第一次上传图片
      uploadImageFirst: true,
      dataForm: {
        id: 0,
        subjectName: "",
        aDescribtion: "",
        bDescribtion: "",
        cDescribtion: "",
        dDescribtion: "",
        subjectImage: "",
        subjectType: "",
        subjectAnwser: "",
        catagoryId: "",
        score: undefined,
      },
      dataRule: {
        subjectName: [
          { required: true, message: "题目描述不能为空", trigger: "blur" },
        ],
        subjectType: [
          {
            required: true,
            message:
              "题目类型 0-单选 1-多选题 2-填空题 3-判断题 4-大题不能为空",
            trigger: "blur",
          },
        ],
        subjectAnwser: [
          { required: true, message: "题目答案不能为空", trigger: "blur" },
        ],
        score: [
          {
            required: true,
            message: "题目分数必须填写",
            trigger: "blur",
          },
        ],
      },
    };
  },
  methods: {
    /**
     * 上传文件成功回调函数
     */
    uploadSuccess(response, file, fileList) {
      // if(this.uploadImageFirst)
      // {
      //   //说明是第一次上传图片
      //    this.dataForm.subjectImage = response.data;
      // }
      // else
      // {
      //   //说明不是第一次上传图片
      //    this.dataForm.subjectImage = this.dataForm.subjectImage + ";" + response.data
      // }
      // //改为不是第一次上传图片
      // this.uploadImageFirst = false
      // 打印一下返回结果
      console.log(response);
      //将题目图片地址绑定上  
      this.dataForm.subjectImage = response.data;
      //打印一下是否绑定上
      console.log(this.dataForm.subjectImage);
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    init(id) {
      this.dataForm.id = id || 0;
      this.visible = true;
      this.$nextTick(() => {
        this.$refs["dataForm"].resetFields();
        if (this.dataForm.id) {
          this.$http
            .get(`courseSystem/subject/info/${this.dataForm.id}`, {
              headers: {
                token: this.token,
              },
            })
            .then(({ data }) => {
              if (data && data.code === 0) {
                this.dataForm.subjectName = data.subject.subjectName;
                this.dataForm.aDescribtion = data.subject.aDescribtion;
                this.dataForm.bDescribtion = data.subject.bDescribtion;
                this.dataForm.cDescribtion = data.subject.cDescribtion;
                this.dataForm.dDescribtion = data.subject.dDescribtion;
                this.dataForm.subjectImage = data.subject.subjectImage;
                this.dataForm.subjectType = data.subject.subjectType;
                this.dataForm.subjectAnwser = data.subject.subjectAnwser;
                this.dataForm.catagoryId = data.subject.catagoryId;
                this.dialogImageUrl = data.subject.subjectImage;
                this.dataForm.score = data.subject.score;
              }
            });
        }
      });
    },
    // 表单提交
    dataFormSubmit() {
      this.$refs["dataForm"].validate((valid) => {
        if (valid) {
          this.$http
            .post(
              `courseSystem/subject/${!this.dataForm.id ? "save" : "update"}`,
              {
                id: this.dataForm.id || undefined,
                subjectName: this.dataForm.subjectName,
                aDescribtion: this.dataForm.aDescribtion,
                bDescribtion: this.dataForm.bDescribtion,
                cDescribtion: this.dataForm.cDescribtion,
                dDescribtion: this.dataForm.dDescribtion,
                subjectImage: this.dataForm.subjectImage,
                subjectType: this.dataForm.subjectType,
                subjectAnwser: this.dataForm.subjectAnwser,
                catagoryId: this.dataForm.catagoryId,
                score: this.dataForm.score,
              },
              {
                headers: {
                  token: this.token,
                },
              }
            )
            .then(({ data }) => {
              if (data && data.code === 0) {
                this.$message({
                  message: "操作成功",
                  type: "success",
                  duration: 1500,
                  onClose: () => {
                    this.visible = false;
                    this.$emit("refreshDataList");
                  },
                });
              } else {
                this.$message.error(data.msg);
              }
            });
        }
      });
    },
  },
  created() {
    this.token = this.$store.state.user.token;
    console.log(this.token);
  },
};
</script>
